.pocket {
  padding: 150px 0 0 0;
  background: var(--white);
  @include bg-cover;
  @include tablet {
    padding: 50px 0 0 0;
  }
  // .pocket__container

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include tablet {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  // .pocket__title

  &__title {
    margin: 0;
    font-family: "Comfortaa";
    font-weight: 400;
    font-size: 60px;
    line-height: 67px;
    color: var(--blue);
    margin-bottom: 50px;
    @include mobile {
      font-size: 34px;
      line-height: 45px;
    }
  }

  // .pocket__info

  &__info {
    display: flex;
    align-items: flex-end;
    margin-bottom: 50px;
  }

  // .pocket__list-item

  &__list-item {
    margin-bottom: 25px;
    max-width: 292px;
    @include mobile {
      max-width: 400px;
    }
  }

  // .pocket__list-header

  &__list-header {
    margin: 0;
    font-family: "Inter";
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: var(--dark);
    margin-bottom: 5px;
  }

  // .pocket__list-descr

  &__list-descr {
    margin: 0;
    font-family: "Inter";
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: var(--dark);
    @include mobile {
      font-size: 16px;
    }
  }
  // .pocket__btn

  &__btn {
    padding: 18px 197px;
    border: 2px solid var(--blue);
    font-family: "Comfortaa";
    font-weight: 700;
    font-size: 40px;
    line-height: 45px;
    color: var(--blue);
    transition: 0.4s;
    &:hover {
      background: var(--green);
      color: var(--dark);
      transition: 0.4s;
      border-color: transparent;
    }
    @include mobile {
      padding: 14px 40px;
    }
  }
}
