.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 57px;
  z-index: 50;
}
.nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 105px;
  width: 100%;
  @include small-tablet {
    justify-content: center;
    flex-wrap: wrap;
    gap: 35px;
  }

  // .nav__logo-item

  &__logo-item {
    width: 158px;
    height: 35px;
    transition: 0.5s;
    &:hover {
      transition: 0.5s;
      fill: var(--blue);
    }
    @include small-tablet {
      width: 105px;
    }
  }

  // .nav__btn

  &__btn {
    font-family: "Comfortaa";
    font-weight: 700;
    font-size: 20px;
    line-height: 22px;
    padding: 12px 77px 8px 71px;
    color: var(--white);
    background: var(--dark);
    transition: 0.4s;
    &:hover {
      transition: 0.4s;
      background: var(--green);
      color: var(--dark);
    }
    @include small-tablet {
      font-size: 17px;
      padding: 10px 35px;
    }
  }

  // .nav__tel

  &__tel {
    font-family: "Comfortaa";
    font-weight: 700;
    font-size: 20px;
    line-height: 22px;
    color: var(--dark);
    transition: 0.5s;
    &:hover {
      color: var(--blue);
      transition: 0.5s;
    }
    @include mobile {
      color: var(--white);
    }
  }
}
