.gallery {
  padding: 70px 0;
  background: var(--white);
  @include bg-cover;

  // .gallery__mini

  &__mini {
    display: flex;
    align-items: center;
    justify-content: space-between;
    transform: translateY(-300px);
    @include tablet {
      transform: translateY(0);
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  // .gallery__mini-text

  &__mini-text {
    margin: 0;
    max-width: 350px;
    font-family: "Inter";
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: var(--dark);
    @include tablet {
      max-width: 700px;
      text-align: center;
      margin-bottom: 50px;
    }
  }

  // .gallery__mini-items

  &__mini-items {
    max-width: 782px;
    padding: 72px 32px 50px 31px;
    display: flex;
    flex-direction: column;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 0px;
      left: 0;
      z-index: 3;
      width: 100%;
      height: 100%;
      left: 0;
      border: 3px solid #262c27;
      border-radius: 200px 0px 300px;
    }
  }

  // .gallery__mini-top

  &__mini-top {
    margin-bottom: 24px;
  }

  // .gallery__mini-bottom

  &__mini-bottom {
    display: flex;
    align-items: center;
    gap: 30px;
  }

  // .gallery__main

  &__main {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }

  // .gallery__main-item

  &__main-item {
    margin-top: 50px;
    img {
      width: 100%;
      height: 100%;
    }
    &:nth-child(1) {
      grid-column: 1 span;
      @include small-tablet {
        grid-column: 3 span;
      }
    }
    &:nth-child(2) {
      grid-column: 2 span;
      @include small-tablet {
        grid-column: 3 span;
      }
    }
    &:nth-child(3) {
      grid-column: 1 span;
      @include small-tablet {
        grid-column: 3 span;
      }
    }
    &:nth-child(4) {
      grid-column: 1 span;
      @include small-tablet {
        grid-column: 3 span;
      }
    }
    &:nth-child(5) {
      grid-column: 1 span;
      @include small-tablet {
        grid-column: 3 span;
      }
    }
    &:nth-child(6) {
      grid-column: 1 span;
      @include small-tablet {
        grid-column: 3 span;
      }
    }
    &:nth-child(7) {
      grid-column: 2 span;
      @include small-tablet {
        grid-column: 3 span;
      }
    }
  }

  // .gallery__main-link

  &__main-link {
    display: inline-block;
    padding: 96px 50px;
    background: var(--blue);
    font-family: "Inter";
    font-weight: 800;
    font-size: 32px;
    line-height: 39px;
    text-align: center;
    color: var(--white);
    border: 1px solid #0093a1;
    border-radius: 0px 0px 0px 200px;
  }
}
