.technology {
  padding: 907px 0 50px 0;
  background: url(../img/technology.jpg);
  position: relative;
  @include mobile {
    padding: 650px 0 50px 0;
  }
  // .technology__btn

  &__btn {
    position: absolute;
    top: 60%;
    left: 60%;
    transform: translate(-60%, -60%);
  }

  // .technology__content

  &__content {
    max-width: 470px;
    position: relative;
    z-index: 10;
  }

  // .technology__wrapper

  // .technology__title

  &__title {
    margin: 0;
    font-family: "Comfortaa";
    font-weight: 400;
    font-size: 60px;
    line-height: 67px;
    color: var(--dark);
    margin-bottom: 62px;
  }

  // .technology__list-item

  &__list-item {
    font-family: "Inter";
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: var(--dark);
    margin-bottom: 15px;
  }
}
