.info {
  background: url(../img/info.jpg);
  @include bg-cover;
  position: relative;
  padding: 125px 0 50px 0;
  height: 100%;
  &::after {
    content: "";
    background: url(../img/trees.svg);
    @include bg-cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 456px;
    transform: translateY(-160px) rotate(-180deg);
  }

  //.info__container

  &__container {
    position: relative;
  }

  // .info__content

  &__content {
    position: relative;
    z-index: 10;
    display: flex;
    gap: 87px;
    align-items: flex-end;
    @include tablet {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  // .info__text

  &__text {
    display: flex;
    gap: 82px;
    @include tablet {
      flex-direction: column;
    }
  }

  // .info__text-1

  &__text-1 {
    max-width: 290px;
    font-family: "Inter";
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
    @include tablet {
      font-size: 15px;
    }
  }

  //.info__btn
  &__btn {
    margin-top: 80px;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    width: 100%;
  }

  // .info__text-2

  &__text-2 {
    max-width: 290px;
    font-family: "Inter";
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
    padding-bottom: 32px;
    border-bottom: 1px solid var(--white);
    @include tablet {
      font-size: 15px;
    }
  }

  // .info__descr

  &__descr {
    display: flex;
    align-items: center;
    margin-top: 65%;
    @include tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  // .info__title

  &__title {
    margin: 0;
    font-family: "Comfortaa";
    font-weight: 400;
    font-size: 60px;
    line-height: 67px;
    color: var(--white);
    margin-right: 162px;
    @include tablet {
      margin-bottom: 50px;
      margin-right: 0;
    }
  }

  // .info__list

  &__list {
    max-width: 565px;
    padding: 30px 0;
    border-top: 1px solid var(--white);
    border-bottom: 1px solid var(--white);
  }

  // .info__list-item

  &__list-item {
    display: flex;
    align-items: center;
    gap: 25px;
    margin-bottom: 15px;
    @include mobile {
      flex-direction: column;
      align-items: flex-start;
      gap: 3px;
    }
  }

  // .info__list-info

  &__list-info {
    font-family: "Inter";
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
    width: 178px;
    @include mobile {
      width: 120px;
    }
  }

  // .info__list-text

  &__list-text {
    font-family: "Inter";
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
    @include small-tablet {
      font-size: 15px;
    }
  }
}
