.footer {
  background: var(--dark);
  padding: 100px 0;
  // .footer__top

  &__top {
    display: flex;
    gap: 20px;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 97px;
    flex-wrap: wrap;
  }

  // .footer__top-title

  &__top-title {
    margin: 0;
    font-family: "Comfortaa";
    font-weight: 400;
    font-size: 60px;
    line-height: 67px;
    color: var(--white);
    margin-bottom: 37px;
    @include mobile {
      font-size: 35px;
    }
  }

  // .footer__top-link

  &__top-link {
    svg {
      fill: var(--white);
      width: 370.64px;
      height: 81.1px;
      @include mobile {
        width: 270px;
      }
    }
  }

  // .footer__bottom-title

  &__bottom-title {
    margin: 0;
    font-family: "Inter";
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
  }

  // .footer__bottom-photo

  &__bottom-photo {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    gap: 52px;
    flex-wrap: wrap;
    @include mobile {
      justify-content: center;
    }
  }

  // .footer__bottom-nav

  &__bottom-nav {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    gap: 90px;
  }

  // .footer__bottom-link

  &__bottom-link {
    font-family: "Inter";
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
    text-transform: uppercase;
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 50px;
  align-items: flex-start;
  // .form__input
  &__input {
    background: transparent;
    outline: none;
    padding: 0 0 10px 0;
    border-bottom: 1px solid var(--white);
    opacity: 0.4;
    font-family: "Inter";
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: var(--white);
    opacity: 0.2;
  }

  // .form__btn
  &__btn {
    padding: 10px 66px;
    background: var(--white);
    font-family: "Comfortaa";
    font-weight: 700;
    font-size: 20px;
    line-height: 22px;
    color: var(--black);
  }
}
