.hero {
  background: url(../img/hero.jpg);
  @include bg-cover;
  position: relative;
  @include mobile {
    background-position: right;
  }
  &::after {
    content: "";
    background: url(../img/trees.svg);
    @include bg-cover;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 456px;
    transform: translateY(220px);
  }
  //.hero__container

  &__container {
    position: relative;
    height: 100vh;
  }

  // .hero__content

  &__content {
    position: absolute;
    right: 35px;
    top: 50%;
    max-width: 560px;
    z-index: 15;
    @include small-tablet {
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: static;
      top: auto;
      right: auto;
      height: 100%;
    }
  }

  // .hero__title

  &__title {
    margin: 0;
    text-align: center;
    font-family: "Comfortaa";
    font-weight: 700;
    font-size: 64px;
    line-height: 71px;
    color: var(--white);
    margin-bottom: 20px;
    @include small-tablet {
      font-size: 35px;
    }
  }

  // .hero__text

  &__text {
    margin: 0;
    text-align: center;
    font-family: "Comfortaa";
    font-weight: 700;
    font-size: 24px;
    line-height: 27px;
    color: var(--white);
    &:not(:first-child) {
      margin-bottom: 20px;
    }
    @include small-tablet {
      font-size: 17px;
    }
  }
  // .hero__decoration-1

  &__decoration-1 {
    font-family: "Comfortaa";
    font-weight: 400;
    font-size: 130px;
    line-height: 145px;
    color: var(--blue);
    opacity: 0.3;
    position: absolute;
    bottom: 0;
    right: 50%;
    z-index: 4;
    @include small-tablet {
      font-size: 47px;
      left: 0;
    }
  }

  // .hero__decoration-2

  &__decoration-2 {
    font-family: "Comfortaa";
    font-weight: 400;
    font-size: 130px;
    line-height: 145px;
    color: var(--blue);
    opacity: 0.3;
    position: absolute;
    bottom: 85px;
    right: 0;
    z-index: 4;
    @include small-tablet {
      font-size: 47px;
    }
  }

  // .hero__decoration-3

  &__decoration-3 {
    max-width: 360px;
    text-align: center;
    font-family: "Inter";
    font-weight: 300;
    font-size: 32px;
    line-height: 39px;
    color: var(--blue);
    opacity: 0.3;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 4;
    @include small-tablet {
      font-size: 27px;
      transform: translateY(30px);
    }
  }
}
